<template>
  <div class="content">
    <div class="team-h">
      <div class="num">{{ teamList.length }}</div>
      <div class="team">我的团队（人）</div>
    </div>
    <div class="team-list">
      <ul>
        <li v-for="item in teamList" :key="item.id">
          <div class="info1">
            <img :src="item.img" alt />
            <div>
              <div class="name">
                {{ item.name }}
                <span>{{item.vip ? '悦享会员' : '普通会员'}}</span>
              </div>
              <div class="time">{{ item.time }} 注册</div>
            </div>
          </div>
          <div class="info2">
            <div class="tel">{{ item.tel }}</div>
            <div class="pay">累计消费：<span>￥{{ item.payall }}</span></div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      teamList: [
        { 
          id:1,
          img:require('@/assets/personal/avatar.png'),
          name:'未命名1号',
          time:'2017-02-12 14:20:12',
          tel:'136****1001',
          payall:'1252.00',
          vip:'false',
          
        },
        {
          id:2,
          img:require('@/assets/personal/avatar.png'),
          name:'未命名1号',
          time:'2017-02-12 14:20:12',
          tel:'136****1001',
          payall:'1252.00',
          vip:'false'
        },
        {
          id:3,
          img:require('@/assets/personal/avatar.png'),
          name:'未命名1号',
          time:'2017-02-12 14:20:12',
          tel:'136****1001',
          payall:'1252.00',
          vip:'true'
        },
        {
          id:4,
          img:require('@/assets/personal/avatar.png'),
          name:'未命名1号',
          time:'2017-02-12 14:20:12',
          tel:'136****1001',
          payall:'1252.00',
          vip:'false'
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.content {
  .team-h {
    height: 220px;
    margin: 21px 24px 40px;
    background: url("../../../assets/personal/bg-t.png") no-repeat;
    background-size: 100% 100%;
    text-align: center;
    color: #fff;
    overflow: hidden;
    .num {
      font-size: 56px;
      margin-top: 35px;
    }
    .team {
      font-size: 24px;
    }
  }
  .team-list {
    background-color: #fff;
    ul {
      li {
        height: 140px;
        padding: 28px 24px;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .info1 {
          display: flex;
          align-items: center;
          img {
            width: 66px;
            height: 66px;
          }
          & > div {
            padding-left: 14px;
            .name {
              color: #222;
              font-size: 28px;
              > span {
                background-color: #d9c186;
                font-size: 20px;
                margin-left: 10px;
                padding: 1px 10px;
                line-height: 28px;
                border-radius: 60px;
                color:#fff;
                &.active{
                  background-color: #F2A94A;
                }
              }
            }
            .time{
              color:#888;
              font-size: 22px;
              margin-top: 13px;
            }
          }
        }
        .info2{
          text-align: right;
          .tel{
            color: #222;
            font-size: 28px;
            margin-bottom: 6px;
          }
          .pay{
            color: #FF4A54;
            font-size: 24px;
            &>span{
              font-size: 26px;
            }
          }
        }
      }
    }
  }
}
</style>




